  right: 50;
  max-height: 5%;
  margin-top: 5%;
}

.carousel-indicators :not(active) {
  /* opacity: 100%; */
  border: 3px solid #002357 !important;
  min-height: 20px;
  max-width: 20px;
  border-radius: 50px;
}

.carousel-indicators .active {
  background-color: #002357;
  height: 20px;
  width: 20px;
  border-radius: 50px;
}

